import React from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Button, PanelHeaderBack, Div, CardGrid, Card} from '@vkontakte/vkui';

import './pageS.css'



const pageS = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="home"/>}
		>
			Я взял лут и вышел?
		</PanelHeader>
		<Div>
			<Button stretched size="l"> Show me the Persik, please </Button>
		</Div>

		<Div> 
		<CardGrid size="l">
          <Card onClick={props.go} data-to="about" className="parent" mode="outline">
            <div style={{ height: 64 }} />
			<Button className='child' size="s"> ты же хочешь нажать?</Button>
			
          </Card>
        </CardGrid>
		</Div>
		
	</Panel>
);

pageS.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default pageS;